import React from 'react';
import { SearchBar } from '../components/SearchBar';
import { SearchList } from '../components/SearchList';

export default class Search extends React.Component {
    constructor(props) {
        //Init props and state
        super(props); //calling a parent constructer
        //to initialize the props from the parent and giv to the child
        //props=() //It is an empty object
        //props is used to passing the data between the components
        this.searchTxtValue='';
        this.state={imagedata:[]};

    }
    //componentDidMount is called itself
    componentDidMount(){
        //this.giveMeTheData();
    }
    giveMeTheData(searchValue){
        const url=`http://api.giphy.com/v1/gifs/search?api_key=YN0VlHTGZpQqcjnRPkP5sw5lELMSxfHr&q=${searchValue}&limit=5`;
        const promise=fetch(url);
        promise.then(response=>{
            console.log("Response is coming",response);
            var p=response.json();
            p.then(result=>{
                console.log("Data is",result.data);
                this.setState({imagedata:result.data});
            }).catch(err=>{
                console.log("Error in json",err);
            })

        }).catch(err=>{
            console.log("Error coming from the server",err);
        })
    }
    takeInput(event){
        let data=event.target.value;
        this.searchTxtValue=data;
        console.log("Calling by child",this.searchTxtValue);
    }
    searchNow(){
        console.log("Search Button is clicked");
        this.giveMeTheData(this.searchTxtValue);
    }
    render() {
        console.log(" RENDER Image data is",this.state.imagedata);
        return (
            <div>
                <h1 className='alert-success text-center'>
        {this.props.title}</h1>
                <SearchBar takeInput={this.takeInput.bind(this)} btClick={this.searchNow.bind(this)}/>
                
                <SearchList imagedata={this.state.imagedata} />
            </div>

        )
    }

    //this.handleEvent = this.handleEvent.bind(this)
}



